
import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';

import { BaseAPIService } from '../base.service';
import { Booking } from './booking.class';
import { HashMap } from '../../../shared/utilities/types.utilities';
import { ServiceManager } from '../service-manager.class';

@Injectable({
    providedIn: 'root'
})
export class BookingsService extends BaseAPIService<Booking> {
    constructor(protected _composer: ComposerService) {
        super(_composer);
        ServiceManager.setService(Booking, this);
        this._name = 'Bookings';
        this._api_route = '/bookings';
        this._compare = (a, b) => !(a.id || '').localeCompare(b.id) || !(a.icaluid || '').localeCompare(b.icaluid);
    }

    public accept(id: string, fields?: HashMap) {
        return this.task(id, 'accept', fields);
    }

    public decline(id: string, fields?: HashMap) {
        return this.task(id, 'concierge_decline', fields);
    }

    protected process(raw_data: HashMap): Booking {
        return new Booking(raw_data);
    }
}
